/* Colors */
$white: white;
$black: black;
$green: #5ED1D8;
$pink: #F776DF;
$pink2: #FC88B3;
$pink3: #DB3CFD;
$pink4: #C84BF4;
$pink5: #D55EEF;
$purple: #A55EF3;
$purple2: #9B62EB;
$orange: #F5A783;
$yellow: #F5CC7B;
$blue: #69B3F5;
$blue2: #5F89E6;
$blue3: #6A88DF;
$lightblue: #C6DCEB;
$lightblue2: #D2E5F1;
$lightblue3: #E2F1FA;
$lightblue4: #81CED6;
$lightblue5: #CBDBE9;
$darkblue: #234E80;
$darkblue2: #304D7C;


/* Typography */
$h1: 55px;
$h2: 36px;
$h3: 24px;
$h4: 20px; 
$h5: 18px;
$h6: 16px;

$h1-tablet: 40px;
$h2-tablet: 30px;
$h3-tablet: 20px;

$heading-font-family: 'Poppins', sans-serif;
$body-font-family: 'Lato', sans-serif;
$extra-font-family: 'Open Sans', sans-serif;

$light: 300;
$regular: 400;
$medium: 500;
$semibold: 600;
$bold: 700;

$box-shadow: 0 10px 40px 0 rgba(0,0,0,0.2);

/* Sizes */
$container: 1310px;
$container-single: 830px;

$offset-gap: 50px;

$half-container-with: calc( ( ( ( 100vw - var(--scrollBarWidth) ) - #{$container} ) / 2 ) + #{$offset-gap} );